import React from "react";
import "../styles/css/Browse.css";
import placeholderImg from "../assets/placeholder-banner.jpg";

const Browse = () => {
  return (
    <div className="browse-container">
      {/* <div>
        <img className="banner-img" src={placeholderImg}></img>
      </div> */}
      <div className="banner-img" style={{ backgroundImage: `url(${placeholderImg})` }}></div>
      <div className="test-bg"></div>
      <div className="testBG2"></div>
    </div>
  );
};

export default Browse;
