import "./App.css";
import Nav from "./components/Nav";
import Browse from "./components/Browse";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Router basename={process.env.PUBLIC_URL}>
        <Nav></Nav>
        <Switch>
          <Route exact path="/">
            <Browse></Browse>
          </Route>
          <Route path="/series">
            <Browse></Browse>
          </Route>
          <Route path="/films">
            <Browse></Browse>
          </Route>
          <Route path="/latest">
            <Browse></Browse>
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
